.Playlist {
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: scroll;
    overflow-x: hidden;
    width:  70vw;
    max-height: 700px;
    padding: 2.27rem 1.16rem;
    background-color:rgba(51, 45, 41, 0.6);
   
  }
  
  .Playlist input {
    width: 100%;
    border: 0;
    outline: 0;
    background: transparent;
    border-bottom: 1px solid #6f6f6f;
    font-family: "Work Sans",Helvetica, sans-serif;
    font-size: 1.1rem;
    color: #fff;
  }

  input::placeholder {
    font-size: 1.1rem;
  }
  
  
  .Playlist-save {
    cursor: pointer;
    width: 10rem;
    padding: 0.7rem 0;
    border-radius: 54px;
    border-width: 0px;
    margin-top: 1.27rem;
    background-color:  #53A6A5;;
    text-align: center;
    font-size: 0.83rem;
    transition: background-color 0.25s;
    color: #fff;
    font-weight: 500;
  }
  
  .Playlist-save:hover {
    background-color:rgba(77, 102, 102,0.7);;
  }
  

  
  @media only screen and (max-width: 1020px) {
    .Playlist {
      width: 90%;
    }
  }