.overlay {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(255, 255, 255, 0.5);
    /* opacity: 0.6; */
    cursor: wait;
  }
  
  .overlay_inner {
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    position: absolute;
  }
  
  .overlay_content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    height: 100vh;
  }
  
  .overlay_content h3 {
    color: #160040;
  }
  
  .loader {
    opacity: 1;
    border-radius: 5%;
    animation: loader-spin infinite 5.5s linear;
    height: 30vmin;
  }
  
  .spinner {
    width: 75px;
    height: 75px;
    display: inline-block;
    border-width: 6px;
    border-color: rgba(0, 0, 0, 0.5);
    border-top-color: #010c3f;
    animation: spin 1s infinite linear;
    border-radius: 100%;
    border-style: solid;
  }
  
  @keyframes spin {
    100% {
      transform: rotate(360deg);
    }
  }
  
  @keyframes loader-spin {
    from {
      transform: rotateY(0);
    }
    to {
      transform: rotateY(360deg);
    }
  }